import { FC, forwardRef } from 'react';
import { styled } from '../../utils';
import { TwBaseProps, FormattedColor } from '../../types';
import {
  DefaultProps,
  Badge as MantineBadge,
  BadgeProps as MantineBadgeProps,
} from '@mantine/core';

export interface BadgeProps
  extends TwBaseProps,
   Omit<MantineBadgeProps, keyof DefaultProps> {
  color?: FormattedColor;
  uppercase?: boolean;
}

const Root = styled(MantineBadge)<MantineBadgeProps>(() => ({}));

export const Badge: FC<BadgeProps> = forwardRef((props, ref) => {
  const { uppercase = true, ...rest } = props;
  const textTransformStyle = uppercase ? {} : { textTransform: 'none' };

  return <Root {...rest} ref={ref} style={{ ...textTransformStyle, fontWeight: 500 }} />;
});
